<script>
export default {
  name: 'AIDiseaseDetection',
  data() {
    return {
      activeTab: 'aerial',
      files: [],
      singleImage: null,
      isAnalyzing: false,
      resultReady: false,
      singleResult: null,
      mapInstance: null,
      heatmapInstance: null,
      layers: { rgb: true, heatmap: true, grid: false },
      // Ê®°ÊãüÊï∞ÊçÆÁªìÊûÑ
      prescriptionData: {
        id: 'TASK-20231201-A',
        totalArea: 150,
        dosage: { high: 20, standard: 100 }
      }
    };
  },
  watch: {
    'layers.heatmap': function(val) {
      if(this.heatmapInstance) val ? this.heatmapInstance.show() : this.heatmapInstance.hide();
    }
  },
  methods: {
    // --- Êñá‰ª∂Â§ÑÁêÜ ---
    handleAerialFiles(e) {
      this.files = [...this.files, ...Array.from(e.target.files)];
    },
    handleDrop(e) {
      this.files = [...this.files, ...Array.from(e.dataTransfer.files)];
    },
    removeFile(index) {
      this.files.splice(index, 1);
    },
    handleSingleFile(e) {
      const file = e.target.files[0];
      if(file) {
        this.singleImage = URL.createObjectURL(file);
        // Ê®°ÊãüÂø´ÈÄüÂàÜÊûê
        setTimeout(() => {
          this.singleResult = { disease: 'Rice Blast', prob: 0.98 };
        }, 1000);
      }
    },

    // --- ÂàÜÊûê‰∏éÂú∞Âõæ ---
    startAnalysis() {
      this.isAnalyzing = true;
      // Ê®°Êãü API ËØ∑Ê±Ç
      // await axios.post('/api/ai/analyze', formData);
      setTimeout(() => {
        this.isAnalyzing = false;
        this.resultReady = true;
        this.$nextTick(() => {
          this.initResultMap();
        });
      }, 1500);
    },

    initResultMap() {
      if (!window.AMap) return;
      this.mapInstance = new window.AMap.Map('diseaseMap', {
        center: [113.32, 23.12], // Á§∫‰æã
        zoom: 16,
        mapStyle: 'amap://styles/normal'
      });

      // Ê∏≤ÊüìÁÉ≠ÂäõÂõæ
      if (window.AMap.Heatmap) {
        this.mapInstance.plugin(["AMap.Heatmap"], () => {
          this.heatmapInstance = new window.AMap.Heatmap(this.mapInstance, {
            radius: 25,
            opacity: [0, 0.8],
            gradient: { 0.5: 'blue', 0.65: 'rgb(117,211,248)', 0.7: 'rgb(0, 255, 0)', 0.9: '#ffea00', 1.0: 'red' }
          });
          // Ê®°ÊãüÁÉ≠ÂäõÊï∞ÊçÆ
          const points = [];
          const center = [113.32, 23.12];
          for(let i=0; i<100; i++) {
             points.push({
               lng: center[0] + (Math.random()-0.5)*0.01,
               lat: center[1] + (Math.random()-0.5)*0.01,
               count: Math.random() * 100
             });
          }
          this.heatmapInstance.setDataSet({ data: points, max: 100 });
        });
      }
    },

    // --- ‰∏öÂä°Èó≠ÁéØ ---
    sendToDrone() {
      alert(`Êåá‰ª§Â∑≤‰∏ãÂèëÔºÅ\n‰ªªÂä°ID: ${this.prescriptionData.id}\nÁõÆÊ†áËÆæÂ§á: ÂΩìÂâçÂú®Á∫ø T40 Êú∫Áæ§\nËØ∑Âú®ÈÅ•ÊéßÂô®Á°ÆËÆ§ÂêØÂä®„ÄÇ`);
    },
    exportPrescription() {
      alert('Ê≠£Âú®‰∏ãËΩΩ SHP Â§ÑÊñπÊñá‰ª∂...');
    },
    submitFeedback(isCorrect) {
      alert(isCorrect ? 'ÊÑüË∞¢ÂèçÈ¶àÔºåÊ†∑Êú¨Â∑≤Á∫≥ÂÖ•Âº∫ÂåñËÆ≠ÁªÉÈõÜ„ÄÇ' : 'Â∑≤Ê†áËÆ∞‰∏∫ËØØÊä•ÔºåËØ∑‰∏ìÂÆ∂ËøõË°åÂ§çÊ†∏„ÄÇ');
    }
  }
};
</script>

